#menu-toggle span {
  display: -webkit-flex;
  display: flex;
  width: 24px;
  height: 1px;
  margin-bottom: 5px;
  position: relative;
  background: #1f1f1f;
  border-radius: 3px;
  z-index: 1;
  -webkit-transform-origin: 2px 0px;
          transform-origin: 2px 0px;
  transition: opacity 1s ease-in-out, -webkit-transform 0.5s linear;
  transition: transform 0.5s linear, opacity 1s ease-in-out;
  transition: transform 0.5s linear, opacity 1s ease-in-out, -webkit-transform 0.5s linear;
}

#menu-toggle span:first-child {
  -webkit-transform-origin: 0% 100%;
          transform-origin: 0% 100%;
}

#menu-toggle span:nth-last-child(2) {
  -webkit-transform-origin: 0% 0%;
          transform-origin: 0% 0%;
}

#menu-toggle input:checked ~ span:nth-last-child(1) {
  opacity: 1;
  -webkit-transform: rotate(-45deg) translate(-4px, 3px);
          transform: rotate(-45deg) translate(-4px, 3px);
}

#menu-toggle input:checked ~ span:nth-last-child(2) {
  opacity: 0;
  -webkit-transform: rotate(0deg) scale(0.2, 0.2);
          transform: rotate(0deg) scale(0.2, 0.2);
}

#menu-toggle input:checked ~ span:nth-last-child(3) {
  -webkit-transform: rotate(45deg) translate(2px, 2px);
          transform: rotate(45deg) translate(2px, 2px);
}

